

$tablet: 812px; // smaller sized screens --> decr size by about 1/2 
@mixin tablet {
    @media all and (max-width: #{$tablet}) {
        @content;
    }
}

body {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
