.filter-label { 
  display: block; /* allow bottom and top margin to be set (push away filter labels above or below) */ 
  background: transparent;
  transition: all 0.3s ease 0s;
  position: relative;
  margin-inline-end: 1.7rem;
  display: flex;
  align-items: center;
  min-height: 2rem;
  min-width: 12rem;
  min-height: 3.4rem;
  text-align: center;
  border-radius: 1.7rem;
  border: 1px solid var(--color-white);
  

  &:nth-child(2){
      padding-inline-start: 2.5rem;
  }



  

  span{
    display: inline-block;
    margin: 0 !important;
    text-align: center !important;
    width: 100%;
}
  
  &:hover {
    cursor: pointer;
   
  }
  
  @include tablet {
  }
}

.filter-input {
	position: absolute; /* align over label.. good for better for accessibilty on some touch devices for discoverability. https://css-tricks.com/the-checkbox-hack/ */
	display: flex;
  z-index: 1;
  opacity: 0; /* hide */
   
  &:checked + label { // adjacent sibling selector
   
    box-shadow: none;
  }
}


// ******************************************************************** Gallery container ************************************************************************ //


.filter-wrap {
  position:relative; 
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: row;
  grid-gap: 20px;
  justify-content: flex-start;
  margin: 0;
  font-size: 75%;
  border-radius: 1.7rem;
  overflow: hidden;

  .place-item{
    filter: saturate(1) !important;
    margin-top: 0;
    &:first-child{
        margin: 0;
    }
        .distance-info{
            display: flex !important;
            align-items: baseline;
            span{
                margin: 0;
            }
        }
  }
  
  @include tablet {
   
  }
}

// The grid-cards in the gallery
.filter-item {
  position: relative;

  &:hover {
    transition: all 0.9s ease both;
  }
}

.img-container {
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}

.filter-item img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* crops image top and bottom in this case */
  cursor: pointer;
  transition: all 0.9s ease;
  
  @include tablet {
    height: 83.375px;
  }
}

.img-adjust-1 {
  object-position: 0;  
}

.img-adjust-2 {
  object-position: 100% 100%;  
}

.grid-card-text {
  padding-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  line-height: 5px;
  
  @include tablet {
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
    line-height: 3.3px;
  }
}

.common-name {
  font-size: 17px;
  font-weight: 600;
  line-height: 1.2em;
  @include tablet {
    font-size: 10px;
    line-height: 1.5em;
    margin-bottom: 8px;
  }
}

.scientific-name {
  font-size: 15px;
  line-height: 20px;
  font-style: italic;
  @include tablet {
    height: 30px;
    font-size: 10px;
    line-height: 1.5em;
  }
}

.img-container {
  position: relative;
}

.img-container img {
  display: block;
}

.img-container .fa-exclamation-triangle {
  position: absolute;
  color: color(yellow);
  background-color: color(red);
  border-radius: 3px;
  border: 4px solid color(red);
  top: 0px;
  right: 0px;
  @include tablet {
    font-size: 8px;
  }
}

// ******************************************************************** filtering the gallery  ******************************************************************** //


.filtered-inclusive .filter-item, .filtered-exclusive .filter-item, .filter-no-item { 
	display:none; 
} /* filtered-inclusive/filtered-exclusive class applied via js */

.filtered-inclusive .filter-item.selected, .filtered-exclusive .filter-item:not(.selected) { 
  display: block;
} /* filtered-inclusive/filtered-exclusive class applied via js */

.filter-no-item.filter-no-item-active { /* message stating no filter results */
	display:block; 
	width:100%; 
	background: color(off-white); 
	padding:1em 1.5em; 
  margin:0.75%;
  border-radius:0.25rem; 
  line-height: 1.2rem;
  
  @include tablet {
    line-height: 0.8rem;
  }
}


.filter-mask-active { /* cover each card (animation effect) */
	position:absolute; 
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: color(black);
  animation:filterMask 1s ease-in; 
}
 
@keyframes filterMask { 
	0% { opacity: 1 } 
	100% { opacity: 0 } 
}

.filter-no-item-active {
  animation:filterMaskNoItems 1s ease-in; 
}

@keyframes filterMaskNoItems { 
	0% { opacity: 0; } 
	100% { opacity: 1 }   
}