.slider-img{
    height:37.7rem;
    overflow: hidden;
    img{
       @extend%object-fit;
    }
}

.slider{
    max-width: 75.5rem;

    .slick-arrow{
        z-index: 10;
       bottom: 0;
       transform: translateY(135px);
    }

    .slick-dots {
        text-align: left;
        left:5rem;
        bottom: 4.1rem;
        li button:before,
        li.slick-active button:before {
          color: transparent;
          opacity: 1;
          margin: 5px;
        }
        li button:before{
          background-color: transparent;
          border: 1px solid #fff;
          border-radius: 50%;
          display: inline-block;
          height: 15px;
          width: 15px;
      
        }
        li.slick-active button:before {
          background-color: #fff;
        }

        li{
            margin-inline-end: 1.5rem;
        }
      }
      .slick-prev{
          left: 2.5rem;
          &::before{
            content: "";
            background-image: url(../img/arrow-white-slick.svg);
            background-repeat: no-repeat;
            background-size: contain;
            display: inline-block;
            width: 8px;
            height: 16px;
            transform: scale(-1);
        }
      }
      .slick-next{
          left:15rem;

          &::before{
              content: "";
              background-image: url(../img/arrow-white-slick.svg);
              background-repeat: no-repeat;
              background-size: contain;
              display: inline-block;
              width: 8px;
              height: 16px;
          }
      }
}

